<template>
  <div class="login">
    <div class="bg_img"></div>
    <div class="login-box">
      <div class="login-form">
        <legend>
            <h2>账号密码登录</h2>
        </legend>
        <div class="input-group">
          <el-input
            placeholder="用户名"
            v-model="uname">
          </el-input>
        </div>
        <div class="input-group">
          <el-input
            placeholder="密码"
            v-model="password"
            type="password"
            @keydown.enter.native="login"
            >
          </el-input>
        </div>
        <div>
          <el-button @click="login" type="primary login_btn">登录</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../api'
export default {
  data () {
    return {
      uname: '',
      password: ''
    }
  },
  methods: {
    login () {
      api.login(this.uname, this.password).then(res => {
        window.localStorage.token = res.data.token
        this.$router.push({name: 'issue'})
      })
    }
  }
}
</script>

<style scoped>
.bg_img{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  transition: background-image .3s;
  background-image: url(../assets/login_bg/login-bg-1.jpg);
}
.login_btn{
  display: block;
  width: 100%;
}

.login-box{
    width: 360px;
    position: absolute;
    top: 50%;
    margin-top: -230px;
    right: 200px;
}
.login-form {
    background-color: #fff;
    border-radius: 6px;
    padding: 16px 23px 10px;
}
.login-form>div{
    margin: 25px 0;
}
.login-form legend {
    margin: 0 -20px 20px;
    padding: 0 20px;
    display: block;
    box-sizing: content-box;
    width: 100%;
    line-height: inherit;
    border-bottom: 1px solid #e5e5e5;
}
.login-form legend>h2{
    font-size: 18px;
    font-weight: 600;
    margin: 0 0 -1px;
    line-height: 38px;
    border-bottom: solid 3px #008df3;
    display: inline-block;
}
.login-form .control-group {
    margin-bottom: 6px;
    position: relative;
}

.pass-login-box{
    position: relative;
    top: -10px;
    background: #f3f7fb;
    padding: 10px 20px;
    text-align: center;
}
.pass-login-box a.item {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    margin: 0 6%;
    border: 2px solid #008df3;
    display: inline-block;
    line-height: 43px;
    font-size: 20px;
    color: #008df3;
    speak: none;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.pass-login-box .items{
    margin: 20px 0;
}
.pass-login-box a.item:hover{
    background-color: #008df3;
    color:#fff;
}
@media (max-width:1376px) {
    .login-box{
        width: 300px;
    }
    .login-box{
        right: 100px;
    }
}
@media (min-width:1376px){
    .login-box{
        width: 360px;
    }
}
</style>


