<template>
  <el-menu default-active="1" class="vertical_menu" theme="dark" :router="true" :default-active="currentPath">
    <div class="logo">
        <img src="../assets/logo.png" alt="招生咨询淮南师范学院后台" />
    </div>
    <el-submenu index="general">
      <template slot="title"><i class="el-icon-menu"></i>基本</template>
      <el-menu-item index="/home/issue"><i class="el-icon-document"></i>问题管理</el-menu-item>
      <el-menu-item index="/home/recycle_bin"><i class="el-icon-delete2"></i>回收站</el-menu-item>
    </el-submenu>
  </el-menu>
</template>

<script>
export default{
  data () {
    return {
      currentPath: ''
    }
  },
  mounted () {
    this.currentPath = this.$route.path
  }
}
</script>

<style scoped lang="less">
.vertical_menu{
  width: 240px;
  height: 100%;
  position: fixed!important;
  top: 0;
  bottom: 0;
  left: 0;
  .logo{
    margin: 20px 0;
    text-align: center;
    img{
      background-color: #fff;
      border-radius: 50%;
      border: 3px solid #fff;
      width: 75px;
      height: 75px;
    }
  }
}
</style>



