import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)
Vue.http.options.root = 'http://api.consult.coder4me.cn'
Vue.http.options.emulateJSON = false

export default {
  // 登录
  login (name, password) {
    return ajax('admin/login', 'post', {
      body: {
        name,
        password
      }
    })
  },
  replay (id, answer) {
    return ajax('admin/comment/store', 'post', {
      options: {
        headers: {
          Authorization: 'Bearer ' + window.localStorage.token
        }
      },
      body: {
        issue_id: id,
        answer
      }
    })
  },
  realDeleteIssue (id) {
    return ajax('admin/issue/delete', 'get', {
      options: {
        headers: {
          Authorization: 'Bearer ' + window.localStorage.token
        },
        params: {
          issue_id: id
        }
      }
    })
  },
  restoreIssue (id) {
    return ajax('admin/issue/restore', 'get', {
      options: {
        headers: {
          Authorization: 'Bearer ' + window.localStorage.token
        },
        params: {
          issue_id: id
        }
      }
    })
  },
  deleteIssue (id) {
    return ajax('admin/issue/softdelete', 'get', {
      options: {
        headers: {
          Authorization: 'Bearer ' + window.localStorage.token
        },
        params: {
          issue_id: id
        }
      }
    })
  },
  getTrashedIssueList (offset, limit) {
    return ajax('admin/issue/list_softdel', 'get', {
      options: {
        headers: {
          Authorization: 'Bearer ' + window.localStorage.token
        },
        params: {
          offset,
          limit
        }
      }
    })
  },
  getTrashedIssueListCount () {
    return ajax('admin/soft_issues/softdelete_count', 'get', {
      options: {
        headers: {
          Authorization: 'Bearer ' + window.localStorage.token
        }
      }
    })
  },
  getIssueListCount () {
    return ajax('admin/issues/count', 'get', {
      options: {
        headers: {
          Authorization: 'Bearer ' + window.localStorage.token
        }
      }
    })
  },
  getIssueList (offset, limit) {
    return ajax('admin/issue/lists', 'get', {
      options: {
        headers: {
          Authorization: 'Bearer ' + window.localStorage.token
        },
        params: {
          offset,
          limit
        }
      }
    })
  }
}
/**
 ajax 请求
 @param url
 @param type get|post|put|jsonp ....
 @param options options = {
                      body: request body
                      options: ..,
                      succCallBack: Function
                      errCallBack: Function
                    }
 @return Promise
 */

function ajax (url, type, options) {
  return new Promise(function (resolve, reject) {
    options = options || {}
    if (options.body === undefined) {
      options.body = options.options
      options.options = undefined
    }
    Vue.http[type](url, options.body, options.options).then(res => {
      // 请求成功
      resolve(res)
      if (options.succCallBack !== undefined) {
        options.succCallBack(res)
      }
    }, res => {
      // 请求失败
      Vue.prototype.$message.error(res.data.message)
      reject(res)
      if (options.errCallBack !== undefined) {
        options.errCallBack(res)
      }
      reject(res)
      if (options.errCallBack !== undefined) {
        options.errCallBack(res)
      }
    })
  })
}



