<template>
  <div class="issue view">
    <Panel title="回收站">
      <div class="list">
        <el-table
          v-loading="loading"
          element-loading-text="加载中"
          ref="table"
          :data="issueList"
          style="width: 100%">
          <el-table-column
            type="selection"
            width="50">
          </el-table-column>
          <el-table-column
            prop="id"
            label="ID"
            sortable
            width="100">
          </el-table-column>
          <el-table-column
            prop="title"
            label="标题"
            sortable
            width="220"
            show-tooltip-when-overflow>
          </el-table-column>
          <el-table-column
            prop="created_at"
            sortable
            label="提问时间">
          </el-table-column>
          <el-table-column
            inline-template
            sortable
            label="提问者">
            <span>{{row.wx_users !== null ? row.wx_users.name : '匿名'}}</span>
          </el-table-column>
          <el-table-column
            prop="description"
            sortable
            label="内容">
          </el-table-column>
          <el-table-column
            inline-template
            fixed="right"
            label="操作"
            width="110">
            <span>
              <el-button type="text" size="small" @click="restore(row.id)">恢复</el-button>
              <el-button type="text" size="small" @click="deleteIssue(row.id)">删除</el-button>
            </span>
          </el-table-column>
        </el-table>
        <div class="option">
          <el-pagination
            class="page"
            layout="prev, pager, next"
            @current-change="currentChange"
            :total="total"
            :page-size="pageSize">
          </el-pagination>
        </div>
      </div>
    </Panel>
  </div>
</template>

<script>
  import api from '../api'
  import Panel from '../components/Panel.vue'
  export default {
    components: {
      Panel
    },
    data () {
      return {
        loading: false,
        issueList: [],
        total: 0,
        // 一页显示的数
        pageSize: 10,
        currentPage: 0
      }
    },
    methods: {
      currentChange (page) {
        this.currentPage = page
        this.getTrashedIssueList((page - 1) * this.pageSize, this.pageSize)
      },
      getTrashedIssueList (offset, limit) {
        api.getTrashedIssueList(offset, limit).then(res => {
          this.issueList = res.data
        }, res => {
          if (res.status === 401) {
            this.$router.push({name: 'login'})
          }
        })
      },
      restore (id) {
        api.restoreIssue(id).then(res => {
          this.$message('已恢复')
          this.getTrashedIssueList((this.currentPage - 1) * this.pageSize, this.pageSize)
          api.getTrashedIssueListCount().then(res => {
            this.total = Number(res.data)
          })
        })
      },
      deleteIssue (id) {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          api.realDeleteIssue(id).then(res => {
            this.$message('已删除')
            this.getTrashedIssueList((this.currentPage - 1) * this.pageSize, this.pageSize)
            api.getTrashedIssueListCount().then(res => {
              this.total = Number(res.data)
            })
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      }
    },
    mounted () {
      api.getTrashedIssueListCount().then(res => {
        this.total = Number(res.data)
      })
      this.getTrashedIssueList(0, this.pageSize)
    }
  }
</script>
<style lang="less" scoped>
.issue {
  .option{
    border: 1px solid #e0e6ed;
    border-top: none;
    padding: 10px;
    background-color: #fff;
    position: relative;
    height: 53px;
    button{
      margin-right: 10px;
    }
    >.page{
       position: absolute;
       right: 20px;
       top: 10px;
     }
  }
}
</style>


